body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.custom-btn {
    /*Kraken Purple*/
    background-color: #5848d5 !important; 
    transition-duration: 0.1s !important; 
    color: #ffff !important;
}
.custom-btn:hover {
    /*Lighter version of Kraken Purple*/
    background-color: #8478dc !important; 
}
.link-warning {
    color: #4d4d4e !important;
    -webkit-text-decoration-color: #5848d5 !important;
            text-decoration-color: #5848d5 !important
}

.link-warning:hover {
    color: #bababa !important;
}
/*Highlights to the user an error*/
input.input-error, select.input-error{
    border-color: #ff0000;
}
p.error {
    color:red;
}
